import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { UtilityService } from 'src/app/api/utility.service';
@Component({
  selector: 'app-pay-bills',
  templateUrl: './pay-bills.component.html',
  styleUrls: ['./pay-bills.component.css']
})
export class PayBillsComponent implements OnInit {
  airtimeForm: FormGroup;
  submitted = false;
  success = false;
  loading = false;
  networkList = false;
  showPhone = false;
  constructor(private api: ApiService, private util: UtilityService, private formBuilder: FormBuilder) {
    this.airtimeForm = this.formBuilder.group({
      phone_number: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(11)]],
      network: ['', [Validators.required]],
      amount: ['', [Validators.required]],
      buy_option: ['', [Validators.required]],
      ref: ['' + Date.now() + '']
    });
  }

  ngOnInit() {
    // ;
  }
  loadNetwork() {

  }
  buyAirtime() {
    this.submitted = true;
    if (this.airtimeForm.invalid) {
      return;
    }
    this.loading = true;
    // console.log(this.airtimeForm.value);
    this.api.postMessages(this.airtimeForm.value).then((result: any) => {
      console.log(result);
      this.util.successToast('Message Sent');
      this.loading = false;
      this.airtimeForm = this.formBuilder.group({
        send_name: null,
        recipients: null,
        message_body: null
      });
    }, (err: any) => {
      console.log(err);
      this.util.errorToast('Invalid Credentials');
      this.loading = false;
      console.log(err);
    });
  }
  dataOrAirtime(e) {
    this.showPhone = true;
  }
  phoneBlur(e) {
    const buyoption = this.airtimeForm.value.buy_option;
    this.loading = true;
    if (buyoption === 'buy-airtime') {
      // console.log('Getting Airtime List Service');
      this.api.postAirtimeServices({ phone: this.airtimeForm.value.phone_number }).then((result: any) => {
        console.log(result);
        this.util.successToast('Network Service Ready');
        this.loading = false;
        console.log(result);// remove the airtime service from here
      }, (err: any) => {
        console.log(err);
        this.util.errorToast('Error Getting Network Services');
        this.loading = false;
        console.log(err);
      });
    }
    if (buyoption === 'buy-data') {
      // console.log('Getting Data List Service');
      this.api.postDataServices({ phone: this.airtimeForm.value.phone_number }).then((result: any) => {
        console.log(result);
        this.util.successToast('Network Service Ready');
        this.loading = false;
        console.log(result);// remove the airtime serice from here
      }, (err: any) => {
        console.log(err);
        this.util.errorToast('Error Getting Network Services');
        this.loading = false;
        console.log(err);
      });
    }
  }
}
