import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from '../../api/api.service';
import { UtilityService } from 'src/app/api/utility.service';
import { Phonebook } from 'src/app/models/phonebook';
import { Router } from '@angular/router';
import { Group } from 'src/app/models/group';
@Component({
  selector: 'app-all-contact',
  templateUrl: './all-contact.component.html',
  styleUrls: ['./all-contact.component.css']
})
export class AllContactComponent implements OnInit {
  Groups: Group[];
  Phonebooks: Phonebook[];
  loading: boolean = true;
  contactForm: FormGroup;
  submitted = false;
  success = false;
  constructor(private api: ApiService, private router: Router, private util: UtilityService, private formBuilder: FormBuilder) {
    this.contactForm = this.formBuilder.group({
      name: ['', Validators.required],
      phone_number: ['', Validators.required],
      group_id: ['', Validators.required]
    });
  }

  ngOnInit() {
    this.getAllContacts();
  }
  getAllContacts() {
    this.api.getContacts().then((result: any) => {
      this.Phonebooks = result.data;
      this.getAllGroups();
    }, (err) => {
      this.util.errorToast('Could Not Fetch Contacts from the server');
      console.log(err);
    });
  }
  getAllGroups() {
    this.api.getGroups().then((result: any) => {
      this.Groups = result.groups;
      this.loading = false;
    }, (err) => {
      this.util.errorToast('Could Not Fetch Group from the server');
      console.log(err);
    });
  }
  newContact() {
    // console.log(this.contactForm.value);
    this.submitted = true;
    if (this.contactForm.invalid) {
      return;
    }
    this.loading = true;
    this.api.postContact(this.contactForm.value).then((result: any) => {
      console.log(result);
      this.util.successToast('New Contact Created');
      this.loading = false;
      this.router.navigate(['/contacts']);
      this.contactForm = this.formBuilder.group({
        group_id: null,
        name: null,
        phone_number: null
      });
    }, (err: any) => {
      console.log(err);
      this.util.errorToast('Invalid Credentials');
      this.loading = false;
      console.log(err);
    });
  }

}
