import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { UtilityService } from '../../api/utility.service';
import { Dashboard } from '../../models/dashboard';
import { User } from '../../models/users';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  stats: Dashboard;
  loading: boolean = false;
  profile: User;
  profileForm: FormGroup;
  submitted = false;
  success = false;

  constructor(private api: ApiService, private util: UtilityService, private formBuilder: FormBuilder) {
    this.profileForm = this.formBuilder.group({
      firstName: ['', [Validators.minLength(3), Validators.required]],
      lastName: ['', [Validators.minLength(3), Validators.required]],
      email: ['', [Validators.email, Validators.required]],
      phone: ['', [Validators.required, Validators.minLength(11)]],
      password: ['', [Validators.minLength(8), Validators.required]]
    });
  }

  ngOnInit() {
    this.getProfile();
  }
  getProfile() {
    this.profile = JSON.parse(localStorage.getItem('user'));
    // console.log(this.profile);
    this.profileForm = this.formBuilder.group({
      email: this.profile.email,
      password: this.profile.password,
      firstName: this.profile.firstName,
      lastName: this.profile.lastName,
      phone: this.profile.phone
    });
  }
  updateProfile() {
    this.submitted = true;
    if (this.profileForm.invalid) {
      return;
    }
    this.loading = true;
    this.api.postProfileUpdate(this.profileForm.value).then((result: any) => {
      this.util.successToast('Profile Updated Successfully');
      this.loading = false;
      this.profileForm = this.formBuilder.group({
        email: null,
        password: null,
        firstName: null,
        lastName: null,
        phone: null
      });
    },
      (err: any) => {
        this.util.errorToast(err.error.error);
        this.loading = false;
        console.log(err.error.error);
      });
  }
}
