import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { User } from 'src/app/models/users';
import { ApiService } from 'src/app/api/api.service';
import { UtilityService } from 'src/app/api/utility.service';
@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.css']
})
export class ViewComponent implements OnInit {
  user: User;
  loading = true;
  constructor(private router: Router, private route: ActivatedRoute, private api: ApiService, private util: UtilityService) {

  }

  ngOnInit() {
    this.viewUser(this.route.snapshot.params.affinity_email);
  }
  viewUser(slug: string) {
    this.api.viewUser(slug).then((result: any) => {
      this.util.successToast('User Record Loaded');
      this.user = result.data;
      this.loading = false;
      console.log(this.user);
    },
      (err: any) => {
        this.util.errorToast('Error Loading Users Details');
        this.loading = false;
        console.log(err);
      });
  }

}
