import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api/api.service';
import { UtilityService } from '../api/utility.service';
import { Dashboard } from '../models/dashboard';
import { User } from '../models/users';
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  stats: Dashboard;
  loading: boolean = true;
  profile: User;
  formatedBalance: string = "0.00";
  constructor(private api: ApiService, private util: UtilityService) {
  }

  ngOnInit() {
    // this.loadStats();
    this.getProfile();
  }
  private loadStats() {
    // get the dashboard details
    this.api.dashboardStats().then((result: any) => {
      this.loading = false;
      this.stats = { balance: 1005.45, sent_messages: 7644 };
    }, (err) => {
      console.log(err);
      this.util.errorToast('Could Not Load Page Properly');
      this.loading = false;
    });
  }
  getProfile() {
    this.profile = JSON.parse(localStorage.getItem('user'));
    this.formatedBalance = this.util.formatCurrency(this.profile.balance);
  }

}
