import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { UtilityService } from 'src/app/api/utility.service';
@Component({
  selector: 'app-compose-messages',
  templateUrl: './compose-messages.component.html',
  styleUrls: ['./compose-messages.component.css']
})
export class ComposeMessagesComponent implements OnInit {
  smsForm: FormGroup;
  submitted = false;
  success = false;
  loading = false;
  messageCounter = '0';
  constructor(private api: ApiService, private util: UtilityService, private formBuilder: FormBuilder) {
    this.smsForm = this.formBuilder.group({
      send_name: ['SMS2Profit', [Validators.required, Validators.minLength(3)]],
      recipients: ['', [Validators.required, Validators.minLength(11)]],
      message_body: ['', [Validators.required, Validators.minLength(3)]]
    });
  }

  ngOnInit() {
  }
  countMessage(e) {
    const mssgLength = this.smsForm.value.message_body.length;
    this.messageCounter = 'Page(s): ' + Math.ceil(mssgLength / 160) + ' {Message Lenght: ' + mssgLength + ' } ';
  }
  sendMessage() {
    this.submitted = true;
    if (this.smsForm.invalid) {
      return;
    }
    this.loading = true;
    // console.log(this.smsForm.value);
    this.api.postMessages(this.smsForm.value).then((result: any) => {
      console.log(result);
      this.util.successToast('Message Sent');
      this.loading = false;
      this.smsForm = this.formBuilder.group({
        send_name: null,
        recipients: null,
        message_body: null
      });
    }, (err: any) => {
      console.log(err);
      this.util.errorToast('Invalid Credentials');
      this.loading = false;
      console.log(err);
    });
  }

}
