import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UtilityService } from 'src/app/api/utility.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  submitted = false;
  success = false;
  loading = false;
  constructor(private api: ApiService, private util: UtilityService, private router: Router, private formBuilder: FormBuilder) {
    this.loginForm = this.formBuilder.group({
      email: ['', [Validators.email, Validators.required]],
      password: ['', [Validators.minLength(8), Validators.required]],
      rememberMe: ['true', [Validators.required]],
    });
  }

  ngOnInit() {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    if (localStorage.getItem('token')) {
      localStorage.removeItem('token');
      localStorage.removeItem('user');
    } else {
      localStorage.removeItem('token');
      localStorage.removeItem('user');
    }
  }
  login() {
    this.submitted = true;
    if (this.loginForm.invalid) {
      return;
    }
    this.loading = true;

    this.api.postLogin(this.loginForm.value).then((result: any) => {
      const token = result.token;
      const id = result.userId;
      localStorage.setItem('token', token);
      if (localStorage.getItem('token')) {
        this.util.successToast('Your Login Was Successful');
        this.getUser(id);
      } else {
        localStorage.setItem('token', token);
        this.getUser(id);
      }
    }, (err: any) => {
      this.util.errorToast('Invalid Login Credentials');
      this.loading = false;
      console.log(err);
    });
  }
  getUser(id) {
    this.api.getUser(id).then((result: any) => {
      localStorage.setItem('user', JSON.stringify(result.user));
      this.router.navigate(['/dashboard']);
    }, (err) => {
      console.log(err);
      this.util.errorToast('Could not Get users Details');
      this.loading = false;
      localStorage.removeItem('token');
    });
  }
}
