import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { UtilityService } from 'src/app/api/utility.service';
import { Message } from '../../models/messages';
@Component({
  selector: 'app-sent-messages',
  templateUrl: './sent-messages.component.html',
  styleUrls: ['./sent-messages.component.css']
})
export class SentMessagesComponent implements OnInit {
  Messages: Message[];
  loading = true;
  constructor(private apiService: ApiService, private util: UtilityService) { }


  ngOnInit() {
    this.getSentMessages();
  }
  getSentMessages() {
    this.apiService.getMessages().then((result: any) => {
      this.Messages = result.messages;
      this.loading = false;
    }, (err) => {
      this.util.errorToast('Could Not Fetch Record from the server');
      console.log(err);
    });
  }
}
