import { Component, OnInit } from '@angular/core';
import { Transaction } from '../models/transactions';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiService } from '../api/api.service';
import { UtilityService } from '../api/utility.service';

@Component({
  selector: 'app-transaction-view',
  templateUrl: './transaction-view.component.html',
  styleUrls: ['./transaction-view.component.css']
})
export class TransactionViewComponent implements OnInit {
  Transaction: Transaction;
  loading = true;
  constructor(private router: Router, private route: ActivatedRoute, private api: ApiService, private util: UtilityService) {
  }

  ngOnInit() {
    this.viewTransaction(this.route.snapshot.params.transaction_id);
  }
  viewTransaction(slug) {
    this.api.getTransaction(slug).then((result: any) => {
      this.loading = false;
      this.Transaction = result.data;
    }, (err) => {
      console.log(err);
      this.util.errorToast('Could Not Load Page Properly');
      this.loading = false;
    });
  }
}
