import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UtilityService } from 'src/app/api/utility.service';
@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {
  passwordForm: FormGroup;
  submitted = false;
  success = false;
  loading = false;
  constructor(private api: ApiService, private util: UtilityService, private router: Router, private formBuilder: FormBuilder) {
    this.passwordForm = this.formBuilder.group({
      email: ['', [Validators.email, Validators.required]],
    });
  }

  ngOnInit() {
  }
  resetPassword() {
    this.submitted = true;
    if (this.passwordForm.invalid) {
      return;
    }
    this.loading = true;
    this.api.postResetPassword(this.passwordForm.value).then((result: any) => {
      console.log(result);
      this.util.successToast('An Email has been Sent To The Provided Email');
      this.loading = false;
    }, (err: any) => {
      this.util.errorToast('Email Account Does Not Exist On Our Platform');
      this.loading = false;
      console.log(err);
    });
  }
}
