import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
@Component({
  selector: 'app-dashbar',
  templateUrl: './dashbar.component.html',
  styleUrls: ['./dashbar.component.css']
})
export class DashbarComponent implements OnInit {
  name: string;
  constructor(private api: ApiService, private router: Router, private formBuilder: FormBuilder) {
  }

  ngOnInit() {
    this.getName();
  }
  getName() {
    const user: any = JSON.parse(localStorage.getItem('user'));
    this.name = user.firstName + ' ' + user.lastName;
  }
}
