import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from '../../api/api.service';
import { UtilityService } from 'src/app/api/utility.service';
import { Phonebook } from '../../models/phonebook';
import { Group } from '../../models/group';
import { Router } from '@angular/router';
@Component({
  selector: 'app-all-group',
  templateUrl: './all-group.component.html',
  styleUrls: ['./all-group.component.css']
})
export class AllGroupComponent implements OnInit {
  Phonebooks: Phonebook[];
  Groups: Group[];
  loading = true;
  // for the group form
  groupForm: FormGroup;
  submitted = false;
  success = false;
  constructor(private api: ApiService, private router: Router, private util: UtilityService, private formBuilder: FormBuilder) {
    this.groupForm = this.formBuilder.group({
      group_name: ['', Validators.required]
    });
  }
  ngOnInit() {
    this.getAllGroups();
  }
  getAllGroups() {
    this.api.getGroups().then((result: any) => {
      this.Groups = result.groups;
      this.loading = false;
    }, (err) => {
      this.util.errorToast('Could Not Fetch Record from the server');
      console.log(err);
    });
  }

  createGroup() {
    this.submitted = true;
    if (this.groupForm.invalid) {
      return;
    }
    this.loading = true;
    this.api.postGroup(this.groupForm.value).then((result: any) => {
      console.log(result);
      this.util.successToast('New Group Created');
      this.loading = false;
      this.groupForm = this.formBuilder.group({
        group_name: null
      });
      this.router.navigate(['/groups']);
    }, (err: any) => {
      console.log(err);
      this.util.errorToast('Invalid Credentials');
      this.loading = false;
      console.log(err);
    });
  }
}
