import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { UtilityService } from 'src/app/api/utility.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PaystackOptions } from 'angular4-paystack';
import { Router } from '@angular/router';
@Component({
  selector: 'app-fund-wallet',
  templateUrl: './fund-wallet.component.html',
  styleUrls: ['./fund-wallet.component.css']
})
export class FundWalletComponent implements OnInit {
  fundWalletForm: FormGroup;
  submitted = false;
  success = false;
  loading = false;
  formatedAmount = "0.00";
  options: PaystackOptions = {
    amount: null,
    email: null,
    ref: null
  };
  usersDetails: any;
  paynow = false;
  constructor(private api: ApiService, private router: Router, private util: UtilityService, private formBuilder: FormBuilder) {
    this.fundWalletForm = this.formBuilder.group({
      amount: ['', Validators.required]
    });
  }

  ngOnInit() {
    this.usersDetails = JSON.parse(localStorage.getItem('user'));
  }
  generateInvoice() {
    this.submitted = true;
    if (this.fundWalletForm.invalid) {
      return;
    }
    this.loading = true;
    this.api.postTransactions(this.fundWalletForm.value).then((result: any) => {
      this.options.amount = (result.amount) * 100;
      this.options.ref = result.transactionId;
      this.options.email = this.usersDetails.email;
      this.formatedAmount = this.util.formatCurrency(result.amount);
      this.util.successToast('Proceed To Payment');
      this.loading = false;
      this.fundWalletForm = this.formBuilder.group({
        amount: null
      });
      this.paynow = true;
    },
      (err: any) => {
        this.util.errorToast('Request Failed');
        this.loading = false;
        console.log(err);
      });
  }

  paymentInit() {
    console.log('Payment initialized');
  }

  paymentDone(ref: any) {
    // confirm payment
    this.api.getVerifyTransaction(ref.reference).then((result: any) => {
      this.util.successToast(result.data);
      this.getUser(this.usersDetails.id);
    }, (err: any) => {
      this.util.errorToast('Your Transaction Failed');
    });
  }

  paymentCancel() {
    this.util.errorToast('Transaction Canceled');
  }
  getUser(id) {
    this.api.getUser(id).then((result: any) => {
      localStorage.setItem('user', JSON.stringify(result.user));
      this.router.navigate(['/dashboard']);
    }, (err) => {
      console.log(err);
      this.util.errorToast('Could not Get users Details');
      this.loading = false;
      localStorage.removeItem('token');
    });
  }
}
