import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
// components

import { UsersComponent } from './users/users.component';
import { LoginComponent } from './users/login/login.component';
import { CreateComponent } from './users/create/create.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { LogoutComponent } from './users/logout/logout.component';
import { AbokiGuard } from './mai-guard/aboki.guard';
import { TransactionsComponent } from './transactions/transactions.component';
import { TransactionViewComponent } from './transaction-view/transaction-view.component';
import { MessagesComponent } from './messages/messages.component';
import { ComposeMessagesComponent } from './messages/compose-messages/compose-messages.component';
import { SentMessagesComponent } from './messages/sent-messages/sent-messages.component';
import { PhonebookComponent } from './phonebook/phonebook.component';
import { AllContactComponent } from './phonebook/all-contact/all-contact.component';
import { NewContactComponent } from './phonebook/new-contact/new-contact.component';
import { AllGroupComponent } from './phonebook/all-group/all-group.component';
import { NewGroupComponent } from './phonebook/new-group/new-group.component';
import { FundWalletComponent } from './transactions/fund-wallet/fund-wallet.component';
import { BillsComponent } from './bills/bills.component';
import { BuyAirtimeComponent } from './bills/buy-airtime/buy-airtime.component';
import { PayBillsComponent } from './bills/pay-bills/pay-bills.component';
import { ResetPasswordComponent } from './users/reset-password/reset-password.component';
import { ProfileComponent } from './users/profile/profile.component';
const routes: Routes = [
  { path: '', redirectTo: '/dashboard', pathMatch: 'full' },
  { path: 'dashboard', component: DashboardComponent, canActivate: [AbokiGuard] },
  { path: 'fund-wallet', component: FundWalletComponent, canActivate: [AbokiGuard] },
  { path: 'pay-bills', component: BillsComponent, canActivate: [AbokiGuard], children: [{ path: '', component: PayBillsComponent }] },
  { path: 'buy-airtime', component: BillsComponent, canActivate: [AbokiGuard], children: [{ path: '', component: BuyAirtimeComponent }] },
  { path: 'groups', component: PhonebookComponent, canActivate: [AbokiGuard], children: [{ path: '', component: AllGroupComponent }] },
  { path: 'new-groups', component: PhonebookComponent, canActivate: [AbokiGuard], children: [{ path: '', component: NewGroupComponent }] },
  { path: 'new-contacts', component: PhonebookComponent, canActivate: [AbokiGuard], children: [{ path: '', component: NewContactComponent }] },
  { path: 'contacts', component: PhonebookComponent, canActivate: [AbokiGuard], children: [{ path: '', component: AllContactComponent }] },
  { path: 'sent-messages', component: MessagesComponent, canActivate: [AbokiGuard], children: [{ path: '', component: SentMessagesComponent }] },
  { path: 'compose-message', component: MessagesComponent, canActivate: [AbokiGuard], children: [{ path: '', component: ComposeMessagesComponent }] },
  { path: 'transactions', component: TransactionsComponent, canActivate: [AbokiGuard] },
  { path: 'transactions/view/:transaction_id', component: TransactionViewComponent, canActivate: [AbokiGuard] },
  { path: 'profile', component: UsersComponent, canActivate: [AbokiGuard], children: [{ path: '', component: ProfileComponent }] },
  { path: 'login', component: UsersComponent, children: [{ path: '', component: LoginComponent }] },
  { path: 'forgot-password', component: UsersComponent, children: [{ path: '', component: ResetPasswordComponent }] },
  { path: 'register', component: UsersComponent, children: [{ path: '', component: CreateComponent }] },
  { path: 'logout', component: UsersComponent, canActivate: [AbokiGuard], children: [{ path: '', component: LogoutComponent }] },
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes), RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
