import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { UtilityService } from 'src/app/api/utility.service';
@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css']
})
export class CreateComponent implements OnInit {
  registerForm: FormGroup;
  submitted = false;
  success = false;
  loading = false;
  constructor(private api: ApiService, private util: UtilityService, private formBuilder: FormBuilder) {
    this.registerForm = this.formBuilder.group({
      firstName: ['', [Validators.minLength(3), Validators.required]],
      lastName: ['', [Validators.minLength(3), Validators.required]],
      email: ['', [Validators.email, Validators.required]],
      phone: ['', [Validators.required, Validators.minLength(11)]],
      password: ['', [Validators.minLength(8), Validators.required]]
    });
  }

  ngOnInit() {
  }
  signup() {
    this.submitted = true;
    if (this.registerForm.invalid) {
      return;
    }
    this.loading = true;
    this.api.postSignup(this.registerForm.value).then((result: any) => {
      this.util.successToast('Your Registration Was Successful');
      this.loading = false;
      this.registerForm = this.formBuilder.group({
        email: null,
        password: null,
        firstName: null,
        lastName: null,
        phone: null
      });
    },
      (err: any) => {
        this.util.errorToast(err.error.error);
        this.loading = false;
        console.log(err.error.error);
      });
  }

}
